  margin-top: 40px;
  padding: 40px 0 61.73px;
  background: var(--primary---accent);
}
@media (max-width: 1024px) {
  .footer {
    margin-top: 40px;
  }
}
@media (max-width: 767px) {
  .footer {
    margin-top: 30px;
    padding-top: 22px;
    padding-bottom: 18px;
  }
}
.footer__wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 1225px) {
  .footer__wrapper {
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-wrap: wrap;
  }
}
.footer__container {
  margin-left: 5px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 1225px) {
  .footer__container {
    margin-left: 68px;
    gap: 39px 180px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    margin-top: 27px;
    margin-left: 0;
    gap: 26px 88px;
  }
}
.footer__terms {
  display: flex;
  gap: 20px;
}
@media (max-width: 767px) {
  .footer__terms {
    display: none;
  }
}
.footer__terms_mobile {
  margin-top: 6px;
  margin-left: 24px;
  display: none;
}
@media (max-width: 767px) {
  .footer__terms_mobile {
    display: flex;
  }
}
.footer__terms-item {
  color: var(--primary---white);
  font-size: 16px;
  font-weight: 600;
  line-height: 14.364px;
}
.footer__social {
  margin-left: 111px;
  display: flex;
  gap: 10px;
}
@media (max-width: 1225px) {
  .footer__social {
    margin-left: 4px;
  }
}
@media (max-width: 767px) {
  .footer__social {
    margin-left: 0;
  }
}
.footer__created {
  margin-left: 191px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--primary---white);
  font-size: 16px;
  font-weight: 600;
  line-height: 14.364px;
}
@media (max-width: 1225px) {
  .footer__created {
    margin-left: 0;
  }
}
.footer__copyright {
  margin-left: 79px;
  color: var(--primary---white);
  font-size: 14px;
  font-weight: 400;
  line-height: 159%;
}
@media (max-width: 1225px) {
  .footer__copyright {
    margin-left: 0;
  }
