___CSS_LOADER_EXPORT___.push([module.id, `.header {
  height: 80px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d7d7d7;
  z-index: 20;
}
@media (max-width: 425px) {
  .header {
    height: 55px;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 40px;
}
.header__inventory {
  color: #41456b;
  color: var(--primary---content, #41456b);
  font-size: 25px;
  font-weight: 700;
  line-height: 140%; /* 35px */
  letter-spacing: 0.5px;
}
.header__wrapper {
  display: flex;
  align-items: center;
}
.header__links {
  display: flex;
  gap: 20px;
}
@media (max-width: 640px) {
  .header__links a:first-of-type {
    display: none;
  }
}
.header__burger {
  margin-left: 63px;
  width: 37px;
  height: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}
.header__burger span {
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background: var(--primary---content);
}
@media (max-width: 640px) {
  .header__burger {
    margin-left: 23px;
  }
}
@media (max-width: 425px) {
  .header__burger {
    width: 30px;
    height: 25px;
  }
